import { useCallback, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import KeyboardLayout from '../KeyboardLayout'
import Key from '../Keys/Key'
import { selectLayers, updateKeyBinding } from './layersSlice'
import BindingDialog from '../Binding/BindingDialog'
import keyStyles from '../Keys/styles.module.css'

export default function LayerBindings ({ layout, activeLayer }) {
  const dispatch = useDispatch()
  const layers = useSelector(selectLayers)
  const layer = layers[activeLayer]
  const { bindings } = layer.properties
  const [editBindIndex, setEditBindIndex] = useState(null)

  const handleUpdateBind = useCallback(function (bindIndex, binding) {
    dispatch(updateKeyBinding({ layerIndex: activeLayer, bindIndex, binding }))
  }, [dispatch, activeLayer])

  return (
    <>
      <KeyboardLayout
        layout={layout}
        renderKey={({ index, keyLayout }) => bindings.parsed[index] && (
          <Key
            label={keyLayout.label}
            value={bindings.parsed[index]?.value}
            params={bindings.parsed[index]?.params}
            noBindingClick={true}
            keyWrapperProps={{
              className: keyStyles.unified,
              onClick: event => {
                event.stopPropagation()
                setEditBindIndex(index)
              }
            }}
            onUpdate={bind => handleUpdateBind(index, bind)}
          />
        )}
      />
      {editBindIndex !== null && (
        <BindingDialog
          binding={bindings.parsed[editBindIndex]}
          bindingIndex={editBindIndex}
          onCancel={() => setEditBindIndex(null)}
          onUpdate={bind => {
            handleUpdateBind(editBindIndex, bind)
            setEditBindIndex(null)
          }}
        />
      )}
    </>
  )
}
