import classNames from 'classnames'
import startCase from 'lodash/startCase'
import PropTypes from 'prop-types'

import Modal from '../../Common/Modal'
import DialogBox from '../../Common/DialogBox'
import { useCallback, useMemo, useState } from 'react'
import { BindParam } from './BindParamlist'
import KeyWrapper from '../Keys/KeyWrapper'
import Binding from './Binding'
import useBindingEditor from './binding-editor'

import keyStyles from '../Keys/styles.module.css'
import ValuePicker from '../../ValuePicker'
import BindValue from './BindValue'
import Icon from '../../Common/Icon'
import KeyPositionIndicator from '../Common/KeyPositionIndicator'
import Link from '../../Common/Link'
import ActionButton from '../../Common/ActionButton'

const discussionUrl = 'https://github.com/nickcoutsos/keymap-editor/discussions/109'

export default function BindingDialog (props) {
  const { binding: initialBinding, bindingIndex, onUpdate, onCancel, context = 'none' } = props
  const [binding, setBinding] = useState(initialBinding)
  const [{
    bindTree,
    behavior,
    behaviorParams,
    editing,
    invalid
  }, {
    clearEditing,
    handleUpdateBindValue,
    selectNodeForEditing
  }] = useBindingEditor(binding, context, undefined, setBinding)

  const onConfirm = useCallback(() => {
    onUpdate(binding)
  }, [binding, onUpdate])

  return (
    <Modal onDismiss={onCancel}>
      <div style={{
        position: 'absolute',
        top: 0,
        background: 'var(--bg)',
        borderRadius: '30px',
        boxShadow: '0px 10px 25px rgba(0, 0, 0, 0.4)',
        fontSize: '80%',
        width: '541px',
        padding: '0 20px',
        margin: '20px 0'
      }}>
        <p style={{ margin: '5px 0' }}>
          <Icon name="bullhorn" /> I'm trying out a new way of editing bindings,
          if you hate it then <Link href={discussionUrl} external>please let me
          know</Link>!
        </p>
      </div>
      <DialogBox dismissText={null} style={{ width: '500px' }}>
        <div style={{
          display: 'flex',
          alignItems: 'center',
          justifyContent: 'center',
          borderBottom: '1px solid var(--key-bg-base)',
          paddingBottom: '20px',
          gap: '40px'
        }}>
          <KeyPositionIndicator
            keyPositions={[bindingIndex]}
            styleOverrides={{
              display: 'inline-block',
              padding: '3px',
              borderRadius: '3px'
            }}
            scale={0.15}
            />
          <Icon name="arrow-right" />
          <KeyWrapper selectable={false} style={{ pointerEvents: 'none' }}>
            <Binding
              context={context}
              value={binding.value}
              params={binding.params}
              onUpdate={() => {}}
            />
          </KeyWrapper>
        </div>

        <h2>Set Key Binding</h2>
        <h3>
          Behavior <div
            style={{
              display: 'inline-block',
              fontSize: 'initial',
              fontWeight: 'normal'
            }}
          >
            <span className={keyStyles.extracted}>
              <span className={keyStyles.param}>
                <BindValue
                  slot="behaviour"
                  node={bindTree}
                  expanded={true}
                  onSelect={selectNodeForEditing}
                />
              </span>
            </span>
          </div>
        </h3>

        <Parameters
          slots={behaviorParams}
          node={bindTree}
          onSelect={selectNodeForEditing}
        />

        {editing && (
          <ValuePicker
            node={editing.node}
            type={editing.type}
            behaviour={behavior}
            context={context}
            prompt={editing.prompt}
            searchKey="code"
            onSelect={handleUpdateBindValue}
            onCancel={clearEditing}
          />
        )}

        <div style={{ textAlign: 'center' }}>
          <ActionButton onClick={onConfirm} disabled={invalid}>
            Okay
          </ActionButton>
          <ActionButton onClick={onCancel} secondary>
            Cancel
          </ActionButton>
        </div>
      </DialogBox>
    </Modal>
  )
}

function Parameters ({ slots, node, onSelect }) {
  const styles = {
    listStyleType: slots.length === 1 ? 'none' : undefined
  }

  if (slots.length === 0) {
    return null
  }

  return (
    <>
      <h3>Parameters</h3>
      <ol style={styles}>
        {slots.map((slot, i) => slot.type !== 'placeholder' && (
          <Parameter
            key={i}
            slot={slot}
            param={node.params[i]}
            onSelect={onSelect}
          />
        ))}
      </ol>
    </>
  )
}

function Parameter ({ slot, param, onSelect }) {
  const name = useMemo(() => {
    const simpleNameMap = {
      code: 'key-code'
    }

    const name = slot.raw ? 'Raw value' : (slot.name || slot)
    const mappedName = simpleNameMap[name] || name
    return startCase(mappedName)
  }, [slot])

  return (
    <li style={{ margin: '15px 0' }}>
      <label>{name}</label>
        <span className={classNames(keyStyles.extracted)}>
          <BindParam
            slot={slot}
            node={param}
            onSelect={onSelect}
            expanded={true}
          />
        </span>
    </li>
  )
}

BindingDialog.propTypes = {
  context: PropTypes.string,
  hideBehaviour: PropTypes.bool.isRequired,
  onUpdate: PropTypes.func.isRequired
}

BindingDialog.defaultProps = {
  hideBehaviour: false
}
