import classNames from 'classnames'
import { useContext, useMemo } from 'react'

import { getWrapperStyle } from '../util'
import KeyboardLayout from '../KeyboardLayout'
import KeyWrapper from '../Keys/KeyWrapper'

import styles from './keyPositionSelectorStyles.module.css'
import { LayoutContext } from '../../Contexts/Layout'

export default function KeyPositionIndicator ({ keyPositions, scale = 0.2, styleOverrides = {} }) {
  const { layout, wrapperDimensions } = useContext(LayoutContext)
  const wrapperStyle = useMemo(() => getWrapperStyle(wrapperDimensions, {
    scale,
    overrides: {
      position: 'relative',
      pointerEvents: 'none',
      padding: 0,
      margin: 0,
      ...styleOverrides
    }
  }), [scale, wrapperDimensions, styleOverrides])

  return (
    <div style={wrapperStyle}>
      <KeyboardLayout
        layout={layout}
        bindings={[]}
        scale={scale}
        renderKey={({ index }) => (
          <KeyWrapper
            onClick={() => {}}
            selectable={false}
            themeClass={classNames({
              [styles.key]: true,
              [styles.highlight]: keyPositions.includes(index)
            })}
          />
        )}
      />
    </div>
  )
}
